.navbar {
    display: flex;
    align-items: center;
    padding: 1px;
    padding-right: 5px;
    color: #1E1E1E ;
    /* z-index:1; */
    /* box-shadow: 0px 4px 4px rgba(239, 235, 235, 0.5); */
}
.navbar__image{
    width: 30%;
}
.navbar__title {
    /* margin-right: auto;
    font-size: 150%; */
    padding: 12px 16px; 
    margin-left: 2px;
 
}
.header {
    position: fixed;
    top: 0;
    left: -300px; /* Move the navbar off-screen to the left */
    width: 300px;
    height: 100vh;
    background-color: #fff;
    z-index: 1000;
    transition: all 0.3s ease-in-out;
  }
  
  /* Show the navbar when the 'showMenu' state is true */
  .header.show-menu {
    left: 0;
  }
  

.navbar__item {
    padding: 8px 6px; 
    /* 16px 12px; */
    cursor: pointer;
    padding-left:0.4rem;
    padding-right: 0.4rem;;
    vertical-align: middle;
}
.navbar__button{
    padding: 6px 15px 6px;
    cursor: pointer;
    /* font-size: 104%; */
    background-color: #FF5757;
    color: #ffffff ;
    font-family: Helvetica;
    font-weight: 300;
    height: auto;
}
.navbar__button :hover{
    color: inherit;
    background-color: inherit;
}

.responsive{
    position: absolute;
    z-index: 10;
    top: 16px;
    right: 0;
    width: 40px;
    margin-top: 2px;
    transform-origin: top right;
    background-color: white;
    border-radius: 0.375rem;
    box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
    border: 1px solid black;
    outline: none;
}

.white{
    background-color: white !important;
}